@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');
body{
  letter-spacing: .5px;
  box-sizing: border-box;
}
a, p, ul, ol, h1, h2, h3, h4{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
button{
  background-color: transparent;
  border: none;
  cursor: pointer;
}
/* header */
header{
  font-family: 'Source Sans 3', sans-serif;
  position: sticky;
  top: 0;
  z-index: 10;
}
.hd-bar{
  display: flex;
  padding: 6px 12px;
  background-color: #3c3c3c;
}
.hd-remote-btn{
  display: inline-block;
}
.remote-btn{
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50px;
}
.remote-btn{
  transition: all .5s;
}
.exit{
  background-color: #ED695E;
  margin-right: 8px;
}
.exit:hover{
  background-color: #ED2C1C;
}
.min{
  background-color: #F4BE50;
  margin-right: 8px;
}
.min:hover{
  background-color: #F2A405;
}
.max{
  background-color: #61C454;
}
.max:hover{
  background-color: #29981B;
}
.title{
  margin: 0 auto;
  color: #ccc;
  font-size: 12px;
}
/* main */
.view-layout{
  display: flex;
}
.menu,
.folders {
  position: relative;
  z-index: 3;
  font-family: 'Source Sans 3', sans-serif;
  position: fixed;  /* 추가된 부분 */
  height: 100vh;
  overflow-y: auto; /* 스크롤이 필요한 경우에만 스크롤이 보이도록 설정 */
}
.menu{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0px;
  width: 45px;
  height: calc(100% - 70px);
  background-color: #333;
}
.menu>div{
}
.main-menu>*
,.setting-menu>*{
  margin-bottom: 15px;
}
.folders{
  width: 200px;
  height: 100vh;
  margin-left: 45px;
  background-color: #252526;
}
.explorer{
  display: flex;
  justify-content: space-between;
  padding: 8px 20px;
  border-bottom: 1px solid rgba(255,255,255, .05);
  color: #bbb;
}
.explorer>p{
  display: inline-block;
  font-size: 14px;
}
.explorer>span{
  padding: 0px 5px;
  border-radius: 5px;
}
.explorer>span:hover{
  cursor: pointer;
  background-color: rgba(255,255,255, .1);
}
.pf-folder{
  padding: 10px 0px;
  text-align: start;
}
.folder-li .closed {
  opacity: 0;
}
.pf-folder-tt{
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #ccc;
}
.folder-name{
  display: flex;
  align-items: center;
  margin: 3px 0 3px 20px;
  padding: 5px 0px;
  font-size: 14px;
  color: #ccc;
}
.folder-name>img{
  margin-right: 6px;
}
.selected{
  background-color: #37373D;
}
/* contents */
.contents{
  margin-left: 245px;
  /* width: calc(100% - 245px); */
  width: 100%;
  height: auto;
  min-height: 100vh;
  background-color: #1E1E1E;
  overflow: auto;
  color: #fff;
}
.contents>*{
  font-family: 'JetBrains Mono', sans-serif;
}
.locate{
  background-color: #1e1e1e;
  box-shadow: 5px 5px 5px 2px rgba(0,0,0, .3);
  position: fixed;
  top: 33px;
  width: 100%;
  z-index: 10;
}
.locate-bar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.locate-folder{
  display: flex;
}
.locate-folder li{
  margin-right: 1px;
  font-size: 14px;
  font-family: 'Source Sans 3', sans-serif;
}
.locate-folder img{
  margin-right: 6px;
}
.locate-folder a{
  display: flex;
  color: #ccc;
  font-size: 12px;
  align-items: center;
}
.tab-folder{
  padding: 11px 20px;
  background-color: #2D2D2D;
}
.selected-tab{
  background-color: #1E1E1E;
}
.sub-btn{
  position: fixed;
  right: 0;
  display: flex;
  align-items: center;
  padding: 7px 20px;
}
.sub-btn img{
  opacity: .5;
  transition: all .5s;
}
.sub-btn img:hover{
  opacity: 1;
}
.sub-locate-box{
  padding: 10px 20px;
  text-align: start;
  font-family: 'Source Sans 3', sans-serif;
  color: #ccc;
  font-size: 12px;
}
.main-contents{
  display: inline-block;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.main-contents>img{
  margin-bottom: 50px;
}
.welcome{
  white-space: nowrap;
  font-size: 32px;
  margin-bottom: 50px;
  border-right: 2px solid #fff;
  animation: blink .7s infinite, typing 2s steps(22);
  overflow: hidden;
}
@keyframes blink{
  50%{
    border-color: transparent;
  }
}
@keyframes typing{
  from{
    width: 0;
  }
  to{
    width: 100%;
  }
}
.about-btn{
  padding: 20px;
  font-size: 24px;
  color: #fff;
  background-color: #017BCA;
  border-radius: 10px;
  font-family: 'JetBrains Mono', sans-serif;
}
.pages{
  margin: 100px 25px 0 25px;
  font-family: 'JetBrains Mono', sans-serif;
}

/* About_me */
.about{
  white-space: nowrap;
  padding-bottom: 50px;
  color: #eee;
}
.about div{
  margin-bottom: 15px;
  font-size: 1.1rem;
}
.about span{
  display: inline-block;
}
.about-tt{
  font-size: 24px;
  margin-bottom: 30px;
}
.tt-sub{
  font-size: 14px;
  color: #888;
}
.line-num{
  color: #888;
  margin-right: 20px;
}
.common{
  color: #619DD8;
}
.class{
  color: #A4DCFF;
}
.unique{
  color: #F7894B;
}
.tab1{
  padding-left: 20px;
}
.tab2{
  padding-left: 40px;
}
.tab3{
  padding-left: 60px;
}
.tab4{
  padding-left: 80px;
}
.tab5{
  padding-left: 100px;
}
.tab6{
  padding-left: 120px;
}
.str{
  font-size: 1.4rem;
  color: #fff;
}
/* projects */
.pj-folder{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.pj{
  margin-right: .5%;
  margin-bottom: 25px;
  width: 32.5%;
  height: 100%;
  border-radius: 25px;
  overflow: hidden;
  transition: all .5s;
}
.pj:hover{
  box-shadow: 0 0 15px 0px rgba(255, 255, 255, 0.1);
}
.pj:hover .pj-img>img{
  transform: scale(1.1);
}
.pj-img{
  background-color: #141414;
  overflow: hidden;
}
.pj-img>img{
  width: 100%;
  transition: all .5s;
}
.pj-desc{
  background-color: #141414;
  padding: 20px 20px;
  text-align: center;
}
.pj-tt
,.pj-desc>p
,.pj-desc ul{
  font-size: 14px;
  line-height: 1.5em;
  text-align: start;
}
.pj-tt{
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 1.2rem;
}
.pj-desc>p{
  color: #ddd;
  margin-bottom: 20px;
}
.pj-desc ul{
  margin-bottom: 20px;
}
.pj-desc li{
  display: inline-block;
  border: 1px solid #ddd;
  border-radius: 50px;
  padding: 5px 15px;
  margin-top: 10px;
  margin-right: 10px;
  font-size: 14px;
  color: #ddd;
  transition: all .5s;
}
.pj:hover .pj-desc li{
  border: 1px solid #9CDCFE;
  color: #9CDCFE;
}
.pf-link{
  width: 100%;
  padding: 10px 0px;
  color: #ddd;
  border: 1px solid #ddd;
  border-radius: 50px;
  display: inline-block;
  transition: all .5s;
}
.pj:hover .pf-link{
  color: #fff;
  border: 1px solid #017BCA;
  background-color: #017BCA;

}
.bnr{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.bnr>div{
  border-radius: 25px;
  width: 49%;
  overflow: hidden;
  background-color: #141414;
}
.bnr img{
  width: 100%;
}
.bnr-desc{
  padding: 20px 20px;
}
.bnr-tt{
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 1.2rem;
}
/* contact */
.contact-folder{
  display: flex;
}
.contact-img{
  margin-right: 25px;
}
.contact-img img{
  width: 500px;
}
.my-profile>p{
  font-size: 1.2rem;
  margin-bottom: 15px;
}
.css-tag{
  color: #FFDC8F;
}
.css-method{
  color: #A4DCFF;
}
.css-option{
  color: #B4C9A7;
}
/* footer */
footer{
  width: 100%;
  height :21px;
  position: fixed;
  z-index: 4;
  bottom: 0;
  background-color: #017BCA;
}
